<template>
  <div class="positions-list">
    <div class="position-faction" v-for="position in getPositions" :key="position.id">
        {{ position.name }}
    </div>
  </div>
</template>

<script>
export default {
    computed: {
        getPositions() {
            return this.$store.getters['positions'].sort((a, b) => b.priority - a.priority)
        }
    }
}
</script>

